import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import QSTabs from '@/components/QS-tabs/QS-tabs.vue';

// 网络处理
import { getHomeData,getDetailData } from '@/network/Home'
import { getLoginInfo } from '@/network/Login'
// 变量
import { HOST } from '@/common/const'
// 工具类
import { replaceImage } from '@/utils/detail'
export default{
	components: {
		QSTabs,
		uniLoadMore
	},
	data(){
		return{
			loadingimg:true,//login加载
			loadingType:1,//login状态
			newsList: [], // 新闻
			tabs_3: [], //tabBar
			current_3: 0,
			swiperList: [], // 头部轮播
			homeTitle:[{
				id:0,
				name:'老家特产',
				image:'../../static/homea.png'
			},{
				id:1,
				name:'秒杀',
				image:'../../static/homeb.png',
			},{
				id:2,
				name:'拼团',
				image:'../../static/homec.png'
			},{
				id:3,
				name:'积分商城',
				image:'../../static/homed.png'
			},{
				id:4,
				name:'政府新闻',
				image:'../../static/homed.png'
			},{
				id:5,
				name:'公益信息',
				image:'../../static/homee.png'
			},{
				id:6,
				name:'易物平台',
				image:'../../static/homef.png'
			},{
				id:7,
				name:'更多',
				image:'../../static/homeg.png'
			}],
			productList: [], // 商品列表
		}
	},
	onLoad(){
		// 获取主页上面数据
		this._getHomeData()
		// 获取主页商品列表数据
		this._getDetailData()
		
		// 获取用户信息
		getLoginInfo().then(res => {
			console.log(res)
		})
	},
	methods:{
		
		change(index, i) {
			this['current_' + i] = index;
		},
		//八宫格
		homeListClick(num){
			if(num == 0){
				//老家特产
				uni.navigateTo({
					url:'Native/native'
				})
			}else if(num == 1){
				uni.navigateTo({
					url:'Seckilltime/seckilltime'
				})
			}else if(num == 2){
				//拼团
				uni.navigateTo({
					url:'BookingList/bookingList'
				})
			}else if(num == 3){
				//积分商城
				uni.navigateTo({
					url:'Integral/integralShop'
				})
			}else if(num == 4){
				//政府新闻
				uni.navigateTo({
					url:'Government/government'
				})
			}else if(num == 5){
				//公益信息
				uni.navigateTo({
					url:'PublicMessage/publicmessage'
				})
			}else if(num == 6){
				//易物平台
			}else if(num == 7){
				//更多
			}
		},
		//商品详情页
		detailsClck(id){
			uni.navigateTo({
				url:'../ShopDetails/shopDetails?id=' + id
			})
		},
		// 获取上面数据
		_getHomeData(){
			getHomeData().then(result => {
				const [err,res] = result 
				if(res.statusCode == 200){
					this.swiperList = res.data.data.banner
					this.newsList = res.data.data.roll
					this.tabs_3 = res.data.data.cate
				}
			})
		},
		// 获取商品列表数据
		_getDetailData(){
			getDetailData().then(result => {
				const [err,res] = result
				if(res.statusCode == 200){
					console.log(res)
					const arr = res.data.data.map(item => {
						return replaceImage(item.image)
					})
					this.productList = res.data.data
					this.productList.forEach((item,i) => {
						item.image = arr[i]
					})
				}
			})
		},
		// 跳转到新闻详情页，路径暂时有问题
		detail(url){
			url = `../..${url}`
			uni.navigateTo({
				url:url
			})
		}
	}
}
